// Filename: ToggleSwitch.css 
.container { 
    text-align: center; 
    display: flex;
    justify-content: center;
    gap: 0;
    margin-bottom: 16px;
    } 
    .toggle-switch {
    transform: scale(0.5);     
    position: relative; 
    width: 75px; 
    display: inline-block; 
    text-align: left; 
    top: 8px; 
    } 
    .checkbox { 
    display: none; 
    } 
    .label { 
    display: block; 
    overflow: hidden; 
    cursor: pointer; 
    border: 0 solid #bbb; 
    border-radius: 20px; 
    } 
    .inner { 
    display: block; 
    width: 200%; 
    margin-left: -100%; 
    transition: margin 0.3s ease-in 0s; 
    } 
    .inner:before, 
    .inner:after { 
    float: left; 
    width: 50%; 
    height: 36px; 
    padding: 0; 
    line-height: 36px; 
    color: #fff; 
    font-weight: bold; 
    box-sizing: border-box; 
    } 
    .inner:before { 
    content: ""; 
    padding-left: 10px; 
    background-color: #181818; 
    color: #fff; 
    } 
    .inner:after { 
    content: ""; 
    padding-right: 10px; 
    background-color: #E6E6FA; 
    color: #fff; 
    text-align: right; 
    } 
    .switch { 
    display: block; 
    width: 24px; 
    margin: 5px; 
    background: #fff; 
    position: absolute; 
    top: 0; 
    bottom: 0; 
    right: 40px; 
    border: 0 solid #bbb; 
    border-radius: 20px; 
    transition: all 0.3s ease-in 0s; 
    } 
    .checkbox:checked + .label .inner { 
    margin-left: 0; 
    } 
    .checkbox:checked + .label .switch { 
    right: 0px; 
    }
    .label-text {
        margin-top: 18px;
        color: #e6e6fa;
    } 
    .label-text-dark {
        margin-top: 18px; 
        color: #E6E6FA;
    }
    